@charset 'UTF-8';
@import url('https://fonts.googleapis.com/css2?family=Patua+One&family=Sen:wght@700&display=swap');

* {
    font-family: 'Sen', sans-serif;
    font-size: 1.1rem;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Patua One', cursive;
}

h2 {
    font-size: 2rem;
}

a {
    transition: 0.2s ease-in-out;
}

a:hover {
    opacity: 0.8;
}